.login__logo{
    width:100px;
    object-fit: contain;
    margin-top: 20px;
    margin-bottom: 20px;
}

.login{
    background: white;
    height:100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    

}

.login__container{
    width: 300px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid lightgray;
    height: fit-content;
}

.login__container>h1{
    font-weight: 500;
    margin-bottom: 20px;
}

.login__container > form > h5 {
    margin-bottom: 5px;
}

.login__container > form > input {
    height:30px;
    margin-bottom:20px ;
    width:98%;
    background-color: white;
}

.p{
    margin-top: 15px;
    font-size: 12px;
}

.login__signInButton {
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
  }

  .login__registerButton {
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgray;
  }
  
  